.header {
  -webkit-user-select: none; /* запретить выделение для webkit браузеров */
  -moz-user-select: none; /* запретить выделение для Firefox */
  -ms-user-select: none; /* запретить выделение для IE */
  user-select: none; /* запретить выделение для остальных браузеров */
  background: #ffffff;
  box-shadow: 18px 30px 30px -8px rgba(0, 0, 0, 0.05);
}

.box {
  display: flex;
  align-items: center;
  height: 88px;
  flex: 1 0 100%;
  padding: 0px 30px;
  @media (max-width: 768px) {
    padding: 0px 20px;
  }
}

$burgerWidth: 256px;
$roundDiameter: 40px;
$popupMarginRight: 8px;

.burgerButton {
  margin-right: 20px;
  height: 20px;
  width: 20px;
  display: none;
  @media (max-width: 768px) {
    display: block;
  }
}

.burger {
  padding: 30px 16px 0px;
  display: flex;
  position: absolute;
  content: '';
  width: 256px;
  .burgerItems {
    display: flex;
    flex-direction: column;
    width: 100%;

    .burgerItem {
      padding: 16px;
      height: 55px;
      width: 224px;
      border: 1px solid #e7d4e4;
      margin-bottom: 8px;
      border-radius: 15px;
      &:hover {
        background-color: #e7d4e4;
        transition: background-color 0.2s ease;
      }
    }
    span {
      vertical-align: middle;
      display: flex;
      align-items: center;
      justify-content: flex-start;
      label {
        width: 18px;
        height: 18px;
        background-color: rgb(165, 165, 165);
        border-radius: 50%;
        margin-right: 15px;
      }
    }
  }
  @media (max-width: 768px) {
    display: none;
  }
}

.logo {
  display: flex;
  align-items: center;
  width: 226px;
  @media (max-width: 768px) {
    display: none;
  }
}

.popupFlex {
  flex: 1 0 auto;
}
.popup,
.user {
  height: 60px;
  display: flex;
  align-items: center;
  border-radius: 15px;
  padding: 10px;
  background-color: #dadada43;
  .arrow {
    height: 20px;
    width: 20px;
    transform: rotate(0deg);
  }
  &:hover {
    background-color: #e7d4e4;
    transition: transform 0.3s ease;
    .arrow {
      transition: transform 0.3s ease;
      transform: rotate(180deg);
    }
  }
}

.popup {
  width: fit-content;
  flex: 1 0;
  .plus {
    display: flex;
    justify-content: center;
    align-items: center;
    height: $roundDiameter;
    width: $roundDiameter;
    margin-right: $popupMarginRight;
    background-color: #464a61;
    border-radius: 50%;
  }
  .plusIcon {
    height: 16px;
    width: 16px;
  }
  span {
    text-overflow: clip;
    flex: 1 0;
    margin-right: $popupMarginRight;
    @media (max-width: 835px) {
      width: 70px;
    }
    @media (max-width: 768px) {
      display: none;
    }
  }
}

.user {
  width: fit-content;
  margin: 0;
  .avatar {
    height: $roundDiameter;
    width: $roundDiameter;
    margin-right: $popupMarginRight;
    @media (max-width: 768px) {
      span {
        display: none;
      }
    }
    background-color: green;
    border-radius: 50%;
  }
  .info {
    display: flex;
    flex-direction: column;
    margin-right: $popupMarginRight;
    .mail {
    }
    .id {
    }
    @media (max-width: 768px) {
      span {
        display: none;
      }
    }
  }
}
